@import './variable.css';
.bk-image {
    position: relative;
    display: inline-block;
    overflow: hidden;
    &-inner {
        width: 100%;
        height: 100%;
        vertical-align: top;

        &-center {
            position: relative;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            display: block
        }

    }


    &-error {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: $iconSmallestSize;
        background: $defaultBackgroundColor;
        color: $fnMinorColor;
        vertical-align: middle;
    }

    &-preview {
        cursor: pointer
    }

    &-viewer {
        &-wrapper {
            position: fixed;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0
        }

        &-btn {
            position: absolute;
            z-index: 1;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            opacity: .8;
            cursor: pointer;
            box-sizing: border-box;
            color: $defaultLightColor;
            user-select: none
        }

        &-close {
            top: 80px;
            right: 60px;
            width: 40px;
            height: 40px;
            font-size: 30px;
            color: $defaultLightColor;
            background-color: $newBlackColor1;
            border-color: $defaultLightColor
        }

        &-canvas {
            width: 100%;
            height: 100%;
            display: flex;
            justify-content: center;
            align-items: center
        }

        &-actions {
            left: 50%;
            bottom: 30px;
            transform: translateX(-50%);
            width: 282px;
            height: 44px;
            padding: 0 23px;
            background-color: $fnMainColor;
            border-color: $defaultLightColor;
            border-radius: 22px;
            &-inner {
                width: 100%;
                height: 100%;
                text-align: justify;
                cursor: default;
                font-size: 23px;
                color: $defaultLightColor;
                display: flex;
                align-items: center;
                justify-content: space-around
            }
        }
        &-next, &-prev {
            top: 50%;
            width: 44px;
            height: 44px;
            font-size: 24px;
            color: $defaultLightColor;
            background-color: $newBlackColor1;
            border-color: $defaultLightColor
        }

        &-prev {
            -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
            left: 40px
        }

        &-next {
            -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
            right: 40px;
            text-indent: 2px
        }

        &-mask {
            position: absolute;
            width: 100%;
            height: 100%;
            top: 0;
            left: 0;
            opacity: .5;
            background: #000
        }
    }

}


.viewer-fade-enter-active {
    animation: viewer-fade-in .3s
}

.viewer-fade-leave-active {
    animation: viewer-fade-out .3s
}

@keyframes viewer-fade-in {
    0% {
        transform: translate3d(0, -20px, 0);
        opacity: 0
    }
    100% {
        transform: translate3d(0, 0, 0);
        opacity: 1
    }
}


@keyframes viewer-fade-out {
    0% {
        transform: translate3d(0, 0, 0);
        opacity: 1
    }
    100% {
        transform: translate3d(0, -20px, 0);
        opacity: 0
    }
}
